import { ReactNode } from 'react';
import { InjectedCompProps, styled, SummaryArticle, useBook } from 'topwrite';

interface Props {
    suffix: ReactNode;
    text: ReactNode;
}

export default function Item({ props, Component, context }: InjectedCompProps<Props, SummaryArticle>) {
    let { suffix } = props;
    const book = useBook();

    if (book.getMetadata('pay_read') && context.getMetadata('trial', false) && !suffix) {
        suffix = <Trial>试读</Trial>;
    }

    return <Component {...props} suffix={suffix} />;
}

const Trial = styled.span`
    height: 20px;
    background: #fff3e5;
    line-height: 20px;
    border-radius: 12px;
    padding: 0 8px;
    color: #ff7426;
    font-size: 12px;
    white-space: nowrap;
    margin-right: 5px;
`;
